.profile__header {
    font-style: normal;
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    margin: 0;
    max-width: 294px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media (min-width: 320px) and (max-width: 767px) {
    .profile__header {
        font-size: 27px;
        line-height: 33px;
        text-align: center;
        max-width: 189px;
    }
}